

.alert {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 2;

    flex-direction: column;
    align-items: flex-end;
}

.alert_item {
    font-weight: bold;
    border-radius: .4rem;
    padding: var(--padding_small);
    margin: var(--padding_small);
    width: fit-content;
}


.alert_item_text {
    font-family: var(--fontTitle);
}


.alert_item.primary {
    background-color: var(--blueOpacity);
    color: var(--blue);
}


.alert_item.danger {
    background-color: var(--redOpacity);
    color: var(--red);
}


.alert_item.yellow {
    background-color: var(--blueOpacity);
    color: var(--blue);
}