.logo {
    justify-content: space-between !important;
    border-bottom: .1rem solid var(--gray);
    padding: var(--padding_small);
}

.logo img {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    margin-right: .4rem;
}

.logo p {
    font-weight: bold;
    font-family: 'Gemunu Libre', sans-serif;
    font-size: 1.3rem;
}

