
.cardoverview {
    border: .1rem solid var(--gray);
    padding: var(--padding_small);
    width: 100%;
    flex-direction: column;
    align-items: stretch;
    height: 100%;
    justify-content: flex-start;
}

.cardoverview_head {
    border-bottom: .1rem solid var(--gray);
    padding-bottom: var(--padding_small);
}

.cardoverview_head_title {
    font-weight: bold;
    font-family: 'Gemunu Libre', sans-serif;
    margin-bottom: var(--padding_small);
}

.cardoverview_head_label {
    font-size: .8rem;
    color: var(--grayText);
}

.cardoverview_view {
    justify-content: space-between;
    margin: 0.5rem 0rem;
}

.cardoverview_view_more {
    font-size: .8rem;
    color: var(--blue);
    font-weight: bold;
    cursor: pointer;
}


.cardoverview_body {
    overflow-x: hidden;
}
