#App {
    height: 100vh;
    width: 100vw;
    
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-areas: "sidebar" "main_screen";
}


#sidebar, #main_screen {
    overflow: hidden;
}


#sidebar {
    grid-area: "sidebar";
    height: inherit;
}


#main_screen {
    grid-area: "main_screen";
}