
.competition_item {
    justify-content: flex-start;
    font-size: 0.9rem;
    align-items: flex-start;
    border-bottom: 0.1rem solid var(--gray);
    padding: var(--padding_small);
    transition: all 200ms ease-in;
}


.competition_item:hover {
    background-color: var(--blue);
    color: var(--white);
    cursor: pointer;
    border-radius: .3rem;
}

.competition_item_name {
    font-family: var(--fontTitle);
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: .3rem;
    text-transform: capitalize;
}


.competition_item img {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    margin-right: 1rem;
    object-fit: cover;
}

.competition_item_name_detail {
    font-size: .8rem;
}