@import url('https://fonts.googleapis.com/css2?family=Alkalami&family=Gemunu+Libre:wght@300&family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');


* {
  margin: 0rem;
  padding: 0rem;

  /* font-family: 'Alkalami', serif; */
  /* font-family: 'Gemunu Libre', sans-serif; */
  /* font-family: 'Montserrat', sans-serif; */
  font-family: 'Roboto', sans-serif;

  box-sizing: border-box;
}

:root {

  --white: #ffffff;
  --gray: #eee;
  --grayText: #c5c2c2;
  --blue: #00B0FF;
  --blueOpacity: rgba(0, 176, 255, 20%);
  --black: #000000;
  --blackOpacity: rgba(0, 0, 0, 0.3);
  --red: #e52828;
  --redOpacity: rgba(229, 40, 40, 20%);
  
  --padding_small: .5rem;
  --padding_meduim: 1rem;
  --padding_big: 1.5rem;

  --fontTitle: 'Gemunu Libre', sans-serif;

}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  border: .1rem solid var(--gray);
  padding: calc(var(--padding_small) - .2rem);
  border-radius: 50%;
}

.icon:hover {
  color: var(--blue);
  cursor: pointer;
}


.scroll {
  overflow-y: scroll;
  max-height: 100%;
  padding-right: var(--padding_small);
}

.scroll::-webkit-scrollbar {
  width: .4rem;
}

.scroll::-webkit-scrollbar-thumb {
  background-color: var(--gray);
  border-radius: .2rem;
}



.transition {
  transition: all 200ms ease-in;
}





.button {
  padding: var(--padding_small) var(--padding_big);
  border-radius: 0.5rem;
  outline: none;
  border: 0rem;
  cursor: pointer;
}




.p_title {
  font-family: var(--fontTitle);
}



.padding { padding: var(--padding_meduim );}
.padding_top { padding-top: var(--padding_meduim );}
.padding_bottom { padding-bottom: var(--padding_meduim );}
.padding_right { padding-right: var(--padding_meduim );}
.padding_left { padding-left: var(--padding_meduim );}







input, select {
  outline: 0rem;
  border: 0rem;
}