

.game_item {
    width: 100%;
    padding: var(--padding_small);
    border: .1rem solid var(--gray);
    margin: var(--padding_small);
    border-radius: .5rem;
}

.game_item_vs {
    font-size: 2rem;
}

.game_club img {
    width: 8rem;
    height: 8rem;
    object-fit: cover;
}

.game_club_name {
    margin: var(--padding_small);
    white-space: nowrap;
    font-family: var(--fontTitle);
    font-weight: bold;
}


.game_club_meta {
    flex-direction: column;
}




.game_item_action {
    justify-content: flex-end;
    gap: var(--padding_small);
    margin-bottom: var(--padding_small);
}

.game_item_action i {
    cursor: pointer;
}








.pricing_item {
    width: 100%;
    justify-content: space-between;
    /* gap: 1rem; */
    margin-bottom: 1rem;
}



.pricing_item input, .pricing_item select {
    width: 45%;
}

.pricing_item_trash {
    background-color: var(--redOpacity);
    color: var(--red);
    padding: var(--padding_small);
    border-radius: 50%;
    cursor: pointer;
}