

.player {
    width: fit-content;
    border: .1rem solid var(--gray);
    border-radius: var(--padding_small);
    min-width: 23rem;
    flex-direction: column;
    padding: var(--padding_small);
}


.player:hover {
    background-color: var(--blue);
    color: var(--white);
}



.player_action {
    justify-content: flex-end;
    width: 100%;
    gap: 1rem;
    visibility: hidden;
    /* display: none; */
}



.player_action i {
    cursor: pointer;
}



.player:hover .player_action {
    /* display: inherit; */
    visibility: visible;
}



.player img {
    width: 7rem;
    height: 7rem;
    object-fit: cover;
    border-radius: 50%;
    border: .5rem solid var(--blueOpacity);
}



.player_detail {
    flex-direction: column;
    /* background-color: red; */
    width: 100%;
}


.player_detail h1 {
    font-family: var(--fontTitle);
    font-size: 4rem;
    font-weight: bold;
}


.player_detail div.flex {
    width: 100%;
    justify-content: space-between;
}


.player_detail_value {
    font-family: var(--fontTitle);
    font-weight: bold;
}