

.admin_profile_view {
    /* align-items: flex-start; */
    border: 0.1rem solid var(--gray);
    border-radius: 0.5rem;
    padding: var(--padding_meduim);
    margin: var(--padding_small);
    cursor: pointer;
}


.admin_profile_view:hover {
    background-color: var(--blue);
    color: var(--white);
    border-color: var(--blueOpacity);
}


.admin_profile_view img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: var(--padding_small);
    border: .4rem solid var(--blueOpacity);
}


.admin_profile_view_name {
    font-family: var(--fontTitle);
    white-space: nowrap;
    font-weight: bold;
    font-size: 1.1rem;
}