

.card_info {
    margin-top: var(--padding_meduim);
    justify-content: space-between;
    flex-wrap: wrap;
}

.cardinfoitem {
    border: .1rem solid var(--gray);
    padding: var(--padding_meduim);
    min-width: 18vw;
    justify-content: space-between;
}

.cardinfoitem_title {
    font-weight: bold;
    margin-bottom: var(--padding_small);
    font-family: 'Gemunu Libre', sans-serif;
}

.cardinfoitem_value {
    font-size: .7rem;
}

.cardinfoitem i {
    padding: .7rem;
    border-radius: 50%;
}