

.headersection {
    justify-content: space-between;
    border-bottom: .1rem solid var(--gray);
    padding-bottom: var(--padding_meduim);
    position: sticky;
    top: 0;
    background-color: var(--white);
    z-index: 1;
}

.headersection_title {
    font-weight: bold;
    font-family: 'Gemunu Libre', sans-serif;
    margin-bottom: .3rem;
}

.headersection_time {
    font-size: .80rem;
}

