

.login {
    width: 100vw;
    height: 100vh;
}

.login-bg {
    position: absolute;
    top: 0;
    bottom: 0;
    filter: blur(0.3rem);
}

.login-bg::after {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
}

.login-are {
    width: 22%;
    flex-direction: column;
    gap: var(--padding_small);
    z-index: 1;
    filter: blur(0rem);
}


.login-are button {
    width: 100%;
    background-color: var(--blue);
    color: var(--white);
    font-weight: bold;
}