

.loader {
    padding: var(--padding_meduim);
    border-radius: 50%;
    border: .2rem solid transparent;
    border-top-color: var(--white);
    border-right-color: var(--white);
    animation: loader 1s linear infinite;
}


@keyframes loader {
    to {
        transform: rotate(360deg);
    }
}