

#sidebar_field {
    padding-top: var(--padding_small);
}


#sidebar_field a {
    text-decoration: none;
    color: inherit;
}

.sidebar_field_label {
    justify-content: flex-start;
    font-size: .8rem;
    padding-top: var(--padding_small);
    transition: all .1s ease-in-out;
    padding: calc(var(--padding_small) + .2rem);
}


.sidebar_field_label:hover {
    color: var(--blue);
    border-left: .2rem solid var(--blue);
    background-color: var(--blueOpacity);
    cursor: pointer;
}


.sidebar_field_label i {
    margin-right: .4rem;
}


.sidebar_field_label p {
    font-weight: bold;
}