
.chart_view {
    width: 100%;
    border: 0.1rem solid var(--gray);
    padding: var(--padding_small);
    margin-bottom: var(--padding_meduim);
}


.chart_view_header {
    border-bottom: .1rem solid var(--gray);
    padding: var(--padding_small) 0rem;
    margin-bottom: var(--padding_small);
    justify-content: space-between;
}


.chart_view_tittle {
    font-family: var(--fontTitle);
    font-weight: bold;
}


.chart_view_header .input-date {
    width: fit-content;
}


.chart_view_header select, .chart_view_header option {
    font-family: var(--fontTitle);
    font-weight: bold;
    font-size: 1rem;
}