





.club_header {
    border: .1rem solid var(--gray);
    border-radius: .5rem;
    padding: var(--padding_small);
    width: fit-content;
}

.club_header_title {
    font-family: var(--fontTitle);
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: var(--padding_small);
    border-bottom: .1rem solid var(--gray);
    padding-bottom: var(--padding_small);
}

.club_header img {
    width: 7rem;
    height: 7rem;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 1rem;
}

.club_header span {
    font-weight: bold;
    margin-right: 1rem;
    font-family: var(--fontTitle);

}









.small_presentation_club {
    background-color: var(--gray);
    margin: var(--padding_small);
    padding: calc(var(--padding_small) - 0.3rem) var(--padding_big);
    border-radius: 2rem;
    cursor: pointer;
}

.small_presentation_club p {
    white-space: nowrap;
    margin-right: var(--padding_small);
    font-weight: bold;
}

.small_presentation_club img {
    width: 2rem;
    height: 2rem;
    object-fit: cover;
    border-radius: 50%;
}
