.seller_profile {
    width: fit-content;
    /* min-width: 20vw; */
    border: .1rem solid var(--gray);
    margin: var(--padding_small);
    padding: var(--padding_small);
    justify-content: flex-start;
    align-items: flex-start;
    border-radius: .3rem;
    cursor: pointer;
    transition: all 200ms ease-in;
}


.seller_profile:hover {
    background-color: var(--blue);
    border-color: var(--blue);
    color: var(--white);
}


.seller_profile img {
    width: 130px;
    height: 130px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: var(--padding_meduim);
    border: .4rem solid var(--blueOpacity);
}


.seller_profile_detail {
    padding: var(--padding_small);
}
.seller_profile_name {
    font-family: var(--fontTitle);
    font-size: 1.1rem;
    font-weight: bold;
    white-space: nowrap;
    text-transform: uppercase;
}




.seller_detail_metadata {
    justify-content: space-between;
    align-items: flex-start;
    border-bottom: .1rem solid var(--gray);
    padding-bottom: var(--padding_meduim);
}
.seller_detail_metadata i {
    margin: var(--padding_small);
}
.seller_detail_title {
    font-family: var(--fontTitle);
}





.seller_detail_competition {
    padding: var(--padding_meduim) 0rem;
    border-bottom: .1rem solid var(--gray);
    margin-bottom: var(--padding_meduim);
}