
.scroller {
    width: 100%;
}


.scroller_director {
    border-radius: 50%;
    padding: var(--padding_small);
    background-color: var(--blue);
    color: var(--white);
    cursor: pointer;
}


.scroller_children {
    overflow-x: hidden;
    margin: 0rem var(--padding_meduim);
    justify-content: space-between;
    scroll-behavior: smooth;
    gap: var(--padding_meduim);
}