
/* FORM */


.form {
    align-items: stretch;
    flex-direction: column;
    gap: var(--padding_small);
    width: 100%;
}

.form .button {
    background-color: var(--blue);
    color: var(--white);
    font-weight: bold;
}



/* INPUT */ /* SELECT */
.select-input {
    cursor: pointer;
}
.input-text, .select-input {
    width: 100%;
    border: .1rem solid var(--blue);
    border-radius: .5rem;
    padding: var(--padding_small);
    outline-color: var(--blueOpacity);
}



/* FILE  */

.input-file {
    padding: var(--padding_small);
    border-radius: .4rem;
    border: .1rem solid var(--blue);
    position: relative;
}


.input-file label {
    cursor: pointer;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.input-file-title {
    font-family: var(--fontTitle);
    font-weight: bold;
    font-size: 1.1rem;
}

.input-file-fn {
    font-size: .8rem;
}

.input-file i {
    font-size: 1.1rem;
}
.file-icon {
    background-color: var(--blueOpacity);
    color: var(--blue);
}

.file-icon, .input-file img {
    width: 3rem;
    height: 3rem;
    margin-right: var(--padding_big);
    border-radius: 50%;
}

.input-file img {
    object-fit: cover;
}

.input-file-fn {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }



/* DATE */
.input-date input {
    width: fit-content;
    border: 0rem;
}
.input-date p {
    font-family: var(--fontTitle);
    font-weight: bold;
}