
.club_item {
    justify-content: space-between;
    font-size: 0.9rem;
    align-items: flex-start;
    border-bottom: 0.1rem solid var(--gray);
    padding: var(--padding_small);
    transition: all 200ms ease-in;
}


.club_item:hover {
    background-color: var(--blue);
    color: var(--white);
    cursor: pointer;
    border-radius: .3rem;
}

.club_item_detail_two, .club_item_detail_one {
    font-size: 0.75rem;
}

.club_item_detail_one {
    align-items: flex-start;
}

.club_item_detail_two {
    text-align: right;

}

.club_item_name {
    font-family: var(--fontTitle);
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: .3rem;
    text-transform: capitalize;
}

.club_item_name, .club_item_detail_two p {
    white-space: nowrap;
}

.club_item img {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    margin-right: 1rem;
    object-fit: cover;
}