

.user_info {
    width: 100%;
    justify-content: flex-start;
    border-top: .1rem solid var(--gray);
    /* padding: var(--padding_small); */
    padding: var(--padding_small);
    padding-left: 1rem;
}


.user_info img {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: .3rem solid var(--blueOpacity);
    margin-right: .4rem;
}


.user_info p {
    font-size: 0.82rem;
}

.user_info_name {
    font-weight: bold;
    font-weight: 0.7;
    text-transform: capitalize;
}

.user_info_email {
    opacity: .6;
}