.chip_item {
    border: .1rem solid var(--gray);
    width: fit-content;
    padding: var(--padding_small) var(--padding_big);
    border-radius: var(--padding_big);
    background-color: var(--gray);
    font-weight: bold;
    cursor: pointer;
    margin: var(--padding_small);
    white-space: nowrap;
}


.chip_item:hover, .chip_selected {
    color: var(--white);
    border-color: var(--blue);
    background-color: var(--blue);
}