

.modal {
    position: fixed;
    background-color: var(--blackOpacity);
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 10;
}


.model_body {
    border-radius: var(--padding_small);
    max-width: 70vw;
    max-height: 80vh;
    overflow: hidden;
    background-color: var(--white);
    flex-direction: column;
    background-color: normal;


    justify-content: flex-start;
    align-items: stretch;
}



.model_body_head {
    background-color: var(--blue);
    color: var(--white);
    padding: var(--padding_meduim);
    justify-content: space-between;
}


.model_body_head p {
    font-family: var(--fontTitle);
    font-weight: bold;
}


.model_body_head i {
    cursor: pointer;
    font-weight: bold;

}